.author-comment img{
    width: 60px;
    height: 60px;
}
.author-comment {
    display: flex;
}
.author-comment .info{
    padding-left: 5px;
}

.author-comment .info .email{
    color: blue;
    font-size: 13px;
}

.content-comment .action{
    margin-top: 10px;
}

.content-comment .action span{
    color: blue;
    padding-right: 10px;
    padding-left: 10px;

    border-right: 1px solid gray
}
.content-comment .action span:first-child{
    padding-left: 0px  !important;

}
.content-comment .action span:last-child{
    border-right: 0px !important

}
.content-comment .action span:hover{
/* font-weight: 600; */
}
.content-comment .action .remove{
    color: rgb(228, 31, 31);

}
.content-comment .action .cancel{
    color: red;

}
.content-comment .action .sent-comment{
    color: green;

}
.news-response .title{
    font-weight: 550;
    color: rgb(36, 36, 235);
}
.watch-detail{
    padding-top: 6px;
    color: blue;
    text-decoration: underline;
    font-size: 13px;
}


